	export default {
		data() {
			return {
              mobile:'',
			  verifyCode:'',
			  safety:{
				  time:60,
				  state:false,
				  interval:''
			  }
			};
		},
		onLoad() {

		},
		onShow() {

		},
		methods: {
             getVerifyCode:function(){
				 var _this=this;
				 uni.request({
					 url:this.apiServer+'/user/verify',
					 method:'POST',
					 header:{
						 'content-type':'application/x-www-form-urlencoded'
					 },
					 data:{
						 mobile:_this.mobile
					 },
					 success:res =>{
						 //成功后倒计时60s后可在点击
						 this.safety.state=true;
						 //倒计时
						 this.safety.interval=setInterval(() =>{
							 if(this.safety.time-- <=0){
								 this.safety.time=60;
								 this.safety.state=false;
								 clearInterval(this.safety.interval);
							 }
						 },1000);
						 if(res.data.code===0){
							 uni.showToast({
								 title:'验证码已发送'
							 });
							 _this.disabled=true;
							 console.log(_this.disabled);
						 }else{
							 uni.showModal({
								 title:'提示',
								 content:res.data.msg
							 });
						 }
					 }
				 })
			 },
			 checkCode:function(){
				 var _this=this;
				 console.log(_this.verifyCode);
				 uni.request({
					url: this.apiServer + '/user/check',
					method:'POST',
					header:{
					 'content-type':'application/x-www-form-urlencoded'
					},
					 data:{
						 mobile:_this.mobile,
						 verifyCode:_this.verifyCode
					 },
					 success: res =>{
						 console.log(res.data);
						 if(res.data.code===0){
							 uni.navigateTo({
								 url:'../register_psd/register_psd?mobile='+_this.mobile
							 });
						 }else{
							 uni.showModal({
								 title:'提示',
								 content:res.data.msg
							 });
						 }
					 }
				 });
			 }
		}
	};
